
@use "../../../styles/use/media" as media;
.touch {
    width: 100%;
    height: auto;
    margin: 30px 12px;
    padding: 10px 8px;

		/* .touch__container */
  &__layout__row {
            width: 1050px;
            height: auto;
            display: flex;
            justify-content: space-between;
            align-items: center;
        }

		/* .touch__layout */

		&__layout {
            display: flex;
            flex-flow: column nowrap;
            justify-content: center;
            align-items: center;
            row-gap: 30px;
		}

		/* .touch__title */

		&__title {
            width: 475px;
            height: 62px;
            display: flex;
            flex-flow: column nowrap;
            gap: 10px;
            h4 {
                font-size: 24px;
                line-height: 32px;
                font-weight: bold;
                letter-spacing: 0.1px;
                color: #252B42;

            }
            p {
                font-weight: 500;
                font-size: 14px;
                line-height: 20px;
                text-align: left;
                letter-spacing: 0.2px;

            }
		}

		/* .touch__btn */

		&__btn {
            a {
                color: #FFFFFF;
                background-color: #FF7B47;
                transition: all 0.4s ease-in;
                @include media.hover {
                    background-color: #eaae96;
                }
            }
		}
      
}



