// подключаем медиа-запросы:
@use "../../../styles/use/media" as media;

.header {
    width: 100%;
    height: 104px;

		/* .header__nav-bar */

		&__nav-bar {
            width: 100%;
            height: inherit;
            display: flex;
            flex-flow: row nowrap;
            justify-content: start;
            align-items: center;
            gap: 96px;
            padding: 23px 23px 23px 23px;
            @include media.tablet-b {
                padding: 10px 10px 10px 10px;
                gap: 30px;
            }
		}

		/* .header__logo--bold */

		&__logo--bold {
            width: 187px;
            height: 58px;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            h3 {
    color:  rgba(37, 43, 66, 1);
font-size: 24px;
font-weight: 700;
line-height: calc(32px / 24px * 100%);
letter-spacing: 0.1px;
text-align: left;
@include media.mobile {
    font-size: 20px;
}
            }
		}

		/* .header__links */

		&__links {
            width: 280px;
            height: 24px;
            display: flex;
            justify-content: center;
            align-items: center;
            gap: 21px;
            flex-flow: row nowrap;
            @include media.mobile {
                flex-flow: row wrap;
            }
            
            a {
 color: rgba(115, 115, 115, 1);
font-size: 14px;
font-weight: 450;
line-height: calc(24px / 14px * 100%);
letter-spacing: 0.2px;
text-align: center;
transition: all 0.4s ease;
@include media.hover {
color:  rgba(13, 92, 99, 1);
}

            }
		}

		/* .header__links--active */

		&__links--active {
            color: rgba(255, 123, 71, 1) !important; 

		}
}
