// подключаем медиа-запросы:
@use "../../../styles/use/media" as media;
.cards {
    margin: -135px 0 80px 0; 
    width: 100%;
    height: auto;
		/* .cards__container */

		

		/* .cards__content */

		&__content {
            width: 100%;
            padding: 0 8px 0 8px;
            height: auto;
            display: flex;
            flex-flow: row nowrap;
            justify-content: center;
            align-items: center;
            gap: 24px;
            @include media.tablet-b {
                flex-flow: row wrap;
            }
		}

		/* .cards__item */

		&__item {
            /* card-item */
width: calc(1050px / 3 - 30px);
max-height: 242px;
/* Автолейаут */
display: flex;
flex-direction: column;
justify-content: flex-start;
align-items: flex-start;
padding: 35px 40px 35px 40px;
/* SHADOW */
box-shadow: 0px 13px 19px 0px rgba(0, 0, 0, 0.07);
background: rgba(255, 255, 255, 1);


		}
        &__item--green {
            background:  rgba(13, 92, 99, 1);

        }

		/* .cards__icon */

		&__icon {
width: 48px;
height: 40.71px;
margin: 0 0 24px 0;
		}

		/* .cards__title */

		&__title {
            width: 234px;
           max-height: 64px;
            h3 {
        color: rgba(37, 43, 66, 1);
font-size: 24px;
font-weight: 700;
line-height: calc(32 / 24 * 100%); 
letter-spacing: 0.1px;
text-align: left;
margin: 0 0 24px 0; 
            }
		}

		/* .cards__paragraph */

		&__paragraph {
            width: 200px;
            max-height: 40px;
            
            p {
color:  rgba(115, 115, 115, 1);
font-size: 14px;
font-weight: 500;
line-height: 20px;
letter-spacing: 0.2px;
text-align: left;
            }
		}

		/* .cards__title--white */

		&__title--white {
          
            h3 {
        color:  rgba(255, 255, 255, 1)!important; 
            }
		}

		/* .cards__paragraph--white */

		&__paragraph--white {
            p {
                color:  rgba(255, 255, 255, 1)!important;   
            }
		}
}
