@use "../../../styles/use/media" as media;
// основной код: 
.video {

		/* .video__container */
width: 100%;
height: auto;
min-height: 890px;
padding: 10px 18px;
margin: 30px 12px;
		

		/* .video__layout--column */

		&__layout--column {
            display: flex;
            flex-direction: column;
            flex-wrap: nowrap;
            row-gap: 30px;
		}

		/* .video__layout--row */

		&__layout--row {
            display: flex;
            justify-content: space-between;
            align-items: flex-start;
            gap: 120px;
            flex-direction: row;
            flex-wrap: nowrap;
            @include media.tablet-b {
                flex-wrap: wrap;
                justify-content: center;
                align-items: center;
            }
		}

		/* .video__background */

		&__background {
            width: 400px;
            height: 442px;
            padding: 0 16px;
            display: flex;
            justify-content: center;
            align-items: center;
            @include media.tablet-b {
                padding: 10px 16px;
                align-self: center;
            }
		}

	

		
           

			/* .video__button_play */

			&__button_play {
            width: 100px;
            aspect-ratio: 1;
            border-radius: 73.6px;
            padding: 30px;
            background-color: #0D5C63;
            transition: all 0.4s ease-in;
            @include media.hover {
                background-color: #0cc0d0;
            }
                
			}
		

		/* .video__text */

		&__text {


            @include media.tablet-b {
                display: flex;
                justify-content: center;
                align-items: center;
                flex-flow: column nowrap;
                row-gap: 30px;
            }
		}
        &__text--column {
            
            margin: 16px 8px;
            width: 333px;
            height: 77px; 
            display: flex;
            flex-flow: column nowrap;
            row-gap: 30px;
            justify-content: center;
            align-items: center;


        }
        &__text--row {
            img {
                float: left;
                margin: 0 10px 0 0;
            }
          
            h4 {
                width: 286px;
                height: 48px;
                font-size: 16px;
                line-height: 24px;
                text-align: left;
                letter-spacing: 0.1px;
                color: #252B42;
            }
            p {
                width: 222px;
                height: 24px;
                font-size: 14px;
                line-height: 24px;
                font-weight: semibold; 

            }
        }
        &__text--items {
            margin:  0 0 8px 0;
        }
    }

