@use "../../../styles/use/media" as media;
//  основной код: 
.advice {
    width: 100%;
    height: auto;
    min-height: 699px;
    
		/* .advice__container */


		/* .advice__layout */

		&__layout {
            display: flex;
            flex-direction: column;
            flex-wrap: nowrap;
            row-gap: 90px;
            justify-content: center;
            align-items: center;
		}

		/* .advice__title */

		&__title {
            margin: 30px 0 30px 0; 
            padding: 80px 0; 
		}

		/* .advice__columns */

		&__columns {
            width: 100%;
            max-width: 1050px;
            height: auto;
            min-height: 341px;
            // flex-layout: 
            display: flex;
            justify-content: center;
            align-items: center;
            gap: 30px;
            flex-wrap: nowrap;
            flex-direction: row;
            @include media.tablet-b {
                  flex-wrap: wrap;
            flex-direction: column;
            }
		}

		/* .advice__column--item */

		&__column--item {
            width: calc(1050px / 4 - 30px);
            height: auto;
            max-height: 360px;
            padding: 0 0 0 0;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            flex-direction: column;
            flex-wrap: nowrap;
		}

		/* .advice__item--title */

		&__item--title {
            margin: 0 0 30px 0;
            width: 121px;
            height: 48px;


            p {
                font-size: 16px;
                line-height: 24px;
                font-weight: bold;
                text-align: left;
                color: #252B42;

            }
		}

		/* .advice__item--paragraph */

		&__item--paragraph {
            margin: 0 0 41px 0; 
            width: 160px;
            height: 80px;
            p {
                font-size: 14px;
                line-height: 20px;
                text-align: left;
                font-weight: medium;

            }
		}

		/* .advice__item--image */

		&__item--image {
		}
}
