@mixin  btn{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    // width: var(--btn-max-width);
    // height: var(--btn-max-height);
    max-width: var(--btn-max-width);
    max-height: var(--btn-max-height);
     padding: 10px 36px 10px 36px; 
    border-radius: 37px;
    transition: all 0.4ms ease;
font-size: 14px;
font-weight: 700;
line-height: calc(28px / 14px * 100%);
letter-spacing: 0.2px;
text-align: center;
text-wrap: nowrap;
}

