// подключаем медиа-запросы: 
@use "../../../styles/use/media" as media;

// основной код: 
.experience {
    width: 100%;
    height: auto;
    margin: 80px 0 80px 0;
    min-height: 526px; 
		/* .experience__container */

	

		/* .experience__heading */

		&__heading {
            margin: 0 0 50px 0;
            padding: 8px 12px; 
            display: flex;
            flex-direction: column;
            flex-wrap: nowrap;
            justify-content: center;
            align-items: center;
            row-gap: 32px;

		}

		/* .experience__cards */

		&__cards {
            margin: 50px 0 50px 0; 
            padding: 0 16px 0 16px; 
            display: flex;
            flex-flow: row nowrap;
            justify-content: space-between;
            align-items: center;
            gap: 14px;
            width: 100%;
            height: auto;
            min-height: 209px;
            @include media.tablet {
                flex-flow: row wrap;
                justify-content: center;
            }
		}
}

        .item_ {
            width: calc(1050px / 3 - 30px);
            min-width: 238px;
            height: 209px;
            padding: 10px; 
            background-color: #FFFFFF;
            display: flex;
            flex-direction: column;
            flex-wrap: nowrap;
            row-gap: 16px;
            justify-content: center;
            align-items: center;


	

			/* .item___image */

			&_image {
                width: 48px;
                height: 48px;

              
			}

			/* .item___heading */

			&_heading {
                width: auto;
                max-width: 112px;
                max-height: 57px;

                h2 {
                    font-size: 40px;
                    line-height: calc(57px / 40px * 100%);
                    letter-spacing: 0.2px;
                    font-weight: bold;
                    @include media.tablet {
                        font-size: calc(40px / 1.618px);
                    }

                }
			}

			/* .item___paragraph */

			&_paragraph {
                width: auto;
                max-width: 169px;
                height: 24px;
                
                p {
                    font-size: 16px;
                    line-height: 24px;
                    font-weight: bold;
                    color: #737373!important;
                    text-align: center;

                }
			}
		}



			
		



