// подключаем медиа-запросы:
@use "../../../styles/use/media" as media;
.banner {
    width: 100%;
    max-width: 1440px;
    height: 924px;
    padding: 0 0 0 0;
    margin: 0 0 0 0;

		/* .banner__content--clmn */

		&__content--clmn {
             display: flex;
            flex-flow: column nowrap;
            margin: 80px 15px 80px 15px;
            max-width: 509px;
            max-height: 418px;
		}

		/* .banner__title */

		&__title {
           
            max-width: 509px;
            max-height: 240px;
            margin: 0 0 30px 0;
            padding: 0 0 0 0;
            h1 {
    color: rgba(37, 43, 66, 1);
font-size: 58px;
font-weight: 800;
line-height: calc(80px / 58px * 100%);
letter-spacing: 0.2px;
text-align: left;
text-wrap: balance;
@include media.mobile {
    font-size: calc(58px / 1.618);
}
            }
		}

		/* .banner__subtitle */

		&__subtitle {
            max-width: 350px;
            max-height: 80px;
             margin: 0 0 30px 0;
            padding: 0 0 0 0;
            h4 {
    width: 100%;
    height: 100%;
        color: rgba(115, 115, 115, 1);
font-size: 20px;
font-weight: 400;
line-height: calc(30px / 20px * 100%);
letter-spacing: 0.2px;
text-align: left;
@include media.mobile {
    font-size: calc(20px / 1.618);
}
            }
		}

		/* .banner__btns */

		&__btns {
            width: 330px;
            height: 48px;
            justify-content: space-between;
            align-items: center;
            display: flex;
            flex-flow: row nowrap;
            gap: 8px;
            @include media.tablet {
                flex-flow: row wrap;
            }

		}

		/* .banner__btns--orange */

		&__btns--orange {
            background-color:  rgba(255, 123, 71, 1);
            color:  rgba(255, 255, 255, 1);
            border: none;
		}
        
		/* .banner__btns--transparent */

		&__btns--transparent {
            border: 1px solid  rgba(13, 92, 99, 1);
            background-color: transparent;
            color:rgba(13, 92, 99, 1) ;

		}
}
