@use "../../../styles/use/media" as media;

.logos {
    width: 100%;
    height: 175px;
    margin: -100px 30px 100px 30px;
    @include media.tablet-b {
        margin: -10px 30px 10px 30px;
    }

		/* .logos__container */

		

		/* .logos__layout */

		&__layout {
            display: flex;
            justify-content: space-evenly;
            align-items: center;
            flex-direction: row; 
            flex-wrap: nowrap;
            gap: 32px;
            @include  media.tablet-b {
                flex-wrap: wrap;
                justify-content: center;
            }
		}
}
